@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
  text-decoration: none;
  color: black;
}

.link {
  display: inline-block;
  width: 100px;
  border: 1px solid rgb(198, 223, 235);
  border-radius: 5px;
  color: rgb(80, 106, 255);
  text-decoration: none;
  text-align: center;
  padding: 5px;
  margin: 10px;
}

nav > .link:hover {
  box-shadow: 0px 0px 20px rgb(182, 225, 236);
}

a:hover {
  text-shadow: 0px 0px 20px rgb(121, 80, 255);
}

.header-1 {
  text-align: center;
  -webkit-text-decoration: dashed;
          text-decoration: dashed;
 color: rgb(0, 17, 255);
}
.post {
    padding: 12px;
    display: flex;
    box-shadow: 0 0 7px #607d8b;
    margin: 20px 0px;
    /* border: 1px solid #607d8b; */
}

.avatar {
    flex: 0 0 auto;
}

.oficial-icon {
    width: 24px;
    fill: #4caf50;
}

.avatar img {
    height: 50px;
    width: 50px;
    border-radius: 50%;
    display: block;
    object-fit: cover;
}

.post__container {
    padding-left: 10px;
}

.post__header {
    position: relative;
    font-size: 18px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-family: "Roboto", sans-serif;
}

.post__header-wrapper {
    display: flex;
}

.post-button-icon {
    width: 20px;
    cursor: pointer;
}
.box {
    display: flex;
    flex-flow: wrap;
    justify-content: center;
    align-items: center;
}

.img_Venice {
    width: 23rem;
    margin: 30px 10px;
}

body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Open Sans', sans-serif;
    font-size: 20px;
    background-color: rgb(216, 236, 235);
  }
  
  .wrapper {
    margin: 15px auto;
    border-radius: 20px;
    background-color: rgb(164, 204, 245);
    width: 350px;
    padding: 15px 25px;
    align-items: center;
    align-content: center;
  }
  
  .icon img {
    width: 25px;
    height: 25px;
  }
  
  .phone img {
    width: 25px;
    height: 25px;  
    cursor: pointer;
  }
  
  .container {
    display: flex;
  }
  
  .contacts_wrapper {
    max-width: 300px;
    margin: auto;
  }
  
  .info {
    margin-left: 10px;
  }
  
  .main_info {
    display: flex;
  
  }
  
  .contact {
    font-family: 'Open Sans', sans-serif;
    font-weight: 900px;
    font-size: 30px;
    text-align: center;
    color: rgb(34, 26, 26);
  }
  
  input {
    margin: 25px 62px;
    border: none;
    border-radius: 20px;
    padding: 5px 5px;
    background: rgb(212, 221, 238);
    outline: none;
    height: 30px;
    color: black;
    font-size: 20px;
    font-family: Georgia, 'Times New Roman', Times, serif;
   }
  
  input[placeholder] {
    text-align: center;
  }
  
  .contacts {
    cursor: pointer;
  }
  
  .contacts:hover {
    color: rgb(166, 208, 243);
  }
